@import 'sass-rem';

.input {
  font-size: rem(16px);
  color: #fff;

  label {
    display: block;
    margin-bottom: rem(3px);
  }

  input {
    padding: rem(10px 18px);
    width: 100%;
    border-radius: rem(9px);
    background: linear-gradient(102deg,
        rgba(217, 217, 217, 0.25) 0%,
        rgba(217, 217, 217, 0.1) 0.01%,
        rgba(217, 217, 217, 0.05) 101.02%);
  }
}