@import 'sass-rem';
@import '../../../../assets/styles/helpers/mixins';

.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: rem(16px);
  padding: rem(20px 44px);
  @include frame;
  max-width: 398px;

  &__swiper {}

  &__nav {
    display: flex;
    align-items: center;
    gap: rem(14px);
  }
}

.slide {
  font-size: rem(16px);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: rem(20px);
  //width: 100% !important;
  width: rem(313px) !important;

  &__text {
    color: #DCDCDC;
    text-align: justify;
  }


  &__link {
    border-radius: 16px;
    border: 1px solid #363B4E;
    background: linear-gradient(180deg, #232634 0.01%, #181A24 100%);
    box-shadow: 0px -4px 10px 0px rgba(83, 91, 122, 0.15), 0px 5px 12px 0px rgba(0, 0, 0, 0.25);
    padding: rem(11px);
    color: #FFF;
    display: block;
    text-align: center;
  }
}