@import 'sass-rem';
@import '../../../../assets/styles/helpers/mixins';

.asset {
  @include frame;
  padding: rem(23px);
  flex: 1;
  color: #fff;
  line-height: 1;

  &__inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
  }

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: rem(20px);
    margin-bottom: rem(17px);
  }

  &__title {
    margin-bottom: rem(10px);
  }

  &__percent,
  &__currency {
    color: #F8F8F8;
    font-size: 1.5em;
  }

  &__currency {
    display: flex;
    align-items: flex-start;
    gap: rem(6px);

    span {
      color: rgba(1, 188, 141, 0.60);
      font-size: rem(14px);
    }
  }

  &__right {
    text-align: right;
  }

  &__name {
    color: #FFF;
    font-size: rem(16px);
    margin-bottom: rem(6px);
  }

  &__percent {
    position: relative;
    display: flex;
    align-items: flex-end;
    margin-right: rem(7px);
    gap: rem(5px);

    &::before {
      display: inline-block;
      left: 0;
      bottom: 0;
      background: url('../../../../assets/images/svg/vector.svg') center/contain no-repeat;
      content: '';
      width: rem(45px);
      height: rem(25px);
    }
  }
}

.chart {
  margin-top: auto;
  flex: 1;

  &__title {
    color: rgba(255, 255, 255, 0.50);
    line-height: 1.5;
  }

  &__graph {

    canvas {}
  }
}