@import 'sass-rem';

.modal {
  &__list {
    display: flex;
    flex-direction: column;
    gap: rem(27px);
    height: rem(600px);
    overflow-y: auto;
    margin-right: rem(-10px);
    padding-right: rem(10px);

    &::-webkit-scrollbar {
      width: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: rgba(255, 255, 255, 0.30);
      border-radius: 20px;
    }
  }
}

.wallet {
  display: flex;
  justify-content: space-between;
  gap: rem(10px);
  align-items: center;

  &__info,
  &__content {
    display: flex;

  }

  &__content {
    align-items: center;
    gap: rem(18px);
  }

  &__info {
    flex-direction: column;
    align-items: flex-start;
    color: #FFF;
    line-height: normal;

    span {
      color: rgba(255, 255, 255, 0.50);
      font-size: rem(12px);
    }
  }

  &__icon {
    text-align: center;
    flex: 0 0 rem(52px);
  }

  &__add {
    color: #FFF;

    &:hover {
      text-decoration: underline;
    }
  }
}