@import 'sass-rem';

.slide-btn {
  position: relative;
  width: rem(34px);
  height: rem(34px);
  transition: filter .2s ease-in-out;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: rem(1px) solid rgba(151, 150, 154, 0.80);
  border-radius: 50%;

  &::after {
    background: url('../../images/svg/arrow.svg') center/contain no-repeat;
    position: absolute;
    left: 50%;
    content: '';
    top: 50%;
    transform: translate(-50%, -50%);
    width: rem(13px);
    height: rem(13px);

  }

  &:hover {
    filter: brightness(2);
  }

  &--next {
    transform: rotate(180deg);
  }
}

.referral-nav-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}