@import 'sass-rem';

.header {
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(rem(25px));
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: auto;
  max-height: rem(80px);

  &__inner {
    padding: rem(8px 0 7px);
    display: flex;
    align-items: center;
    gap: rem(49px);
  }

  &__menu {
    margin-right: auto;
  }

  &__info {
    display: flex;
    align-items: center;
    gap: rem(25px);
  }
}

.avatar {
  background-color: linear-gradient(81deg, rgba(255, 255, 255, 0.10) -18.35%, rgba(255, 255, 255, 0.01) 127.69%);
  filter: drop-shadow(0px 20px 40px rgba(0, 0, 0, 0.10));
  backdrop-filter: blur(15.5px);
  border-radius: 50%;
  width: rem(50px);
  height: rem(50px);
  overflow: hidden;
  flex: 0 0 rem(50px);
  border: rem(1px) solid rgba(55, 55, 55, 0.80);

  img {
    object-position: center;
    object-fit: cover;
  }
}

.notify {
  &__icon {
    color: rgba(#fff, .5);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .3s ease-in-out;

    &:hover {
      color: #fff;
    }
  }
}