@import 'sass-rem';
.social {
	&__title {
		margin-bottom: rem(26px);
		color: #fff;
		font-size: rem(30px);
		line-height: 116.667%; /* 116.667% */
	}
	&__list {
		display: flex;
		align-items: center;
		gap: rem(22px);
	}
}
.item {
	transition: 0.3s;
	transform: scale(1);
	display: inline-block;
	&:hover {
		transform: scale(1.1);
	}
}
