@import 'sass-rem';

.walletProfile {
  display: flex;
  gap: rem(10px);

  &__aside {
    flex: 0 0 rem(318px);
    display: flex;
    flex-direction: column;
    gap: rem(10px);
  }

  &__page {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: rem(10px);
  }

  &__row {
    display: flex;
    gap: rem(10px);
  }

  &__search {
    margin-bottom: rem(10px);
  }

  &__balance {
    flex: 0 1 rem(485px);
  }
}