@import 'sass-rem';

.balance {
  background: url('./totalBalansFrame.png') center/cover no-repeat;
  overflow: hidden;
  border-radius: 9px;
  border: 2px solid rgba(95, 95, 95, 0.15);
  backdrop-filter: blur(2px);
  padding: rem(46px 20px 32px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  gap: rem(8px);
  font-size: rem(14px);
  color: #FFF;
  line-height: 1;

  &__total {
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-size: rem(50px);
  }

  &__bonus {
    color: #01BC8D;
  }

}