@import 'sass-rem';

.contact {
  padding-top: rem(50px);
  margin-bottom: rem(120px);
  color: #fff;

  @media(max-width:767.98px) {
    padding-top: 0;
    margin-bottom: rem(60px);
  }

  &__row {
    display: flex;
    gap: rem(20px);
    justify-content: space-between;

    @media(max-width:767.98px) {
      flex-direction: column;
    }
  }

  &__col {
    flex: 0 0 50%;
    max-width: rem(513px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    color: #fff;
    margin-bottom: rem(30px);
  }

  &__text {
    color: #b6b6b6;
  }
}