@import 'sass-rem';

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(#000, .75);
  overflow: hidden;
  overflow-y: auto;
  z-index: 999;

  &__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: rem(20px);
  }

  &__close {
    transition: .2s;
    color: #D9D9D980;
    width: rem(37px);
    height: rem(37px);
    border-radius: 50%;
    border: rem(3px) solid #D9D9D980;

    &:hover {
      border-color: #D9D9D9CC;
      color: #D9D9D9CC;
    }
  }

  &__title {
    color: #fff;
  }

  &__content {
    position: relative;
    border-radius: 24px;
    background: rgba(80, 80, 80, 0.10);
    box-shadow: 0px 0px 0px 15px rgba(0, 0, 0, 0.28);
    backdrop-filter: blur(25px);
    padding: rem(20px);
    min-width: rem(318px);
    margin: rem(20px);
  }
}