@import 'sass-rem';

.team {
  margin-bottom: rem(87px);
}

.slide {
  display: flex;
  flex-direction: column;
  gap: rem(30px);
  align-items: center;
  justify-content: center;
  text-align: center;

  &__img {
    display: inline-block;
    width: rem(183px);
    height: rem(183px);
    flex: 0 0 rem(183px);
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid #0047ff;
  }

  &__content {
    color: #b9b9b9;
  }

  &__social {
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    gap: rem(10px);
  }

  &__icon {
    width: rem(44px);
    flex: 0 0 rem(44px);
    height: rem(44px);
  }
}