@import 'sass-rem';

.wallet {
  padding: rem(22px 38px);

  &__title {
    color: rgba(255, 255, 255, 0.70);
    font-weight: 600;
    margin-bottom: rem(33px);
  }

  &__input {
    &:not(:last-child) {
      margin-bottom: rem(17px);
    }
  }

  &__submit {
    margin-top: rem(33px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.20);
    background: linear-gradient(102deg, rgba(0, 71, 255, 0.50) 0%, rgba(173, 0, 255, 0.53) 101.02%);
    box-shadow: 0px 20px 40px 0px rgba(0, 0, 0, 0.10);
    color: #FFF;
    font-size: rem(16px);
    padding: rem(5px 55px);
    max-width: rem(145px);
  }
}