@import 'sass-rem';

.contact {
  padding: rem(30px);

  &__title {
    color: rgba(255, 255, 255, 0.70);
    font-size: rem(20px);
    font-weight: 600;
    margin-bottom: rem(33px);
  }

  &__form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: rem(17px 35px);
    align-items: end;
  }

  &__save {
    border-radius: rem(16px);
    padding: rem(10px);
    color: #FFF;
    font-size: rem(16px);
    width: rem(145px);
    height: rem(36px);
    margin-bottom: rem(4px);

    &::before {
      border-radius: rem(16px);
    }
  }
}