@import 'sass-rem';
@import '../../../../assets/styles/helpers/mixins';

.block {
  @include frame;
  padding: rem(33px);

  &__text {
    color: #EDEDED;
    font-size: rem(16px);
    line-height: normal;
    margin-bottom: rem(50px);
  }

  &__progress {
    position: relative;

  }

  &__progressLabel {
    color: #E3E3E3;
    font-family: Kanit;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__progressText {
    position: absolute;
    right: 0;
    top: 0;
    color: #E3E3E3;
    font-family: Russo One;
    font-size: rem(16px);
    line-height: normal;

    span {
      font-family: Kanit;
    }
  }

}