@import 'sass-rem';
@import '../../../../assets/styles/helpers/mixins';

.total {
  width: rem(315px);

  &__cards {
    display: flex;
    flex-direction: column;
    gap: rem(10px)
  }
}

.card {
  padding: rem(35px);
  @include frame;
  line-height: 1;
  min-height: rem(118px);
  width: 100%;
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 100px;

  &__head {
    display: flex;
    align-items: center;
    gap: rem(12px);
    //margin-bottom: rem(10px);
  }

  &__name {
    color: rgba(255, 255, 255, 0.60);
  }

  &__bonus {
    color: #01BC8D;
  }

  &__current {
    font-size: rem(30px);
    color: #F8F8F8;
  }
}